const sketchJSFolder: string = process.env.PUBLIC_URL + '/sketch/';
const sketchPDEFolder: string = process.env.PUBLIC_URL + '/sketch/';


export const SketchCode: React.FC<{ folder: string }> = ({ folder }) => {
  const [strJSFullPath, setJSFullPath] = useState<string>(`${sketchJSFolder}${folder}/${folder}.tsx`);
  const [strPDEJSFullPath, setPDEullPath] = useState<string>(`${sketchPDEFolder}${folder}/${folder}.pde`);
  const [functionText, setFunctionText] = useState<string>(''); // 関数のテキストを保存するステート
  const [p5Instance, setP5Instance] = useState<any>(null);
  //const wrapper = React.createRef();
  const wrapper = useRef<HTMLDivElement>(null);

  
  
  // header用
  useEffect(() => {

    if (!strJSFullPath || p5Instance) return;
    console.log(`SketchCode strJSFullPath : ${strJSFullPath}`);

    const script = document.createElement("script");
    script.src = strJSFullPath;
    script.async = true;

    script.onload = () => {
      if (wrapper.current && !p5Instance ) {
        setFunctionText( script.text );
      }
    };

    script.onerror = () => {
      console.error("Failed to load script");
    };

    document.head.appendChild(script);
  }, [strJSFullPath, folder, p5Instance]);


  // スクリプトを読み込む
  useEffect(() => {
    const fetchSketchData = async () => {
      try {
        console.log(`fetchSketchData : ${strJSFullPath}`);
        const fileContent = await readFile(strJSFullPath);
        console.log(`fetchSketchData fileContent : ${fileContent}`);
        //const fileExtractedContent = extractFunctions(fileContent);
        setFunctionText(fileContent);
      } catch (error) {
        console.error('Error fetching sketch data:', error);
      }
    };

    fetchSketchData();
  }, [strJSFullPath]);

  // p5.jsのインスタンスを作成
  useEffect(() => {
    
    /*
    const sketch = (p: p5) => {
      eval(functionText); // 抽出した関数を実行
    };
    */

    //const sketch: (...args: any[]) => any = new Function(functionText) as (...args: any[]) => any;
    
    //eval(functionText); // 抽出した関数を実行
    
   
    const fileContent = require(`./programming/sketch/${folder}`);
    const sketch = fileContent.default; // 仮の関数名
    
    //const sketch: (p: p5) => void = new Function('p', functionText) as (p: p5) => void;

    // p5.jsインスタンスを作成
    if (wrapper.current && !p5Instance && functionText) {
      console.log(`new p5 functionText : ${functionText}`);
      setP5Instance(new p5(sketch,wrapper.current));
    }
    
    return () => {
      if (p5Instance) {
        p5Instance.remove();
        setP5Instance(null);
      }
    };
  }, [functionText]);
  //}, [strJSFullPath]);

  // ファイルパスからファイルを読み込んでテキストを返す
  const readFile = async (filePath: string): Promise<string> => {
    return fetch(filePath).then(response => response.text());
  };

  // 正規表現で関数を抽出
  const extractFunctions = (fileContent: string): string => {

    // 正規表現で関数を抽出
    const functionRegex = /const\s+(\w+)\s*=\s*\(([^)]*)\)\s*=>\s*{([^}]*)};/g;
    const extractedFunctions = [];
    let match;
    while ((match = functionRegex.exec(fileContent)) !== null) {
      const functionName = match[1];
      const functionBody = match[3];
      extractedFunctions.push({ name: functionName, body: functionBody });
    }

    // 抽出した関数を文字列に変換
    const extractedFunctionsText = extractedFunctions.map((func) => {
      return `Function Name: ${func.name}\nFunction Body:\n${func.body}\n`;
    }).join('\n');

    console.log(`extractFunctions : ${extractedFunctionsText}`);
    return extractedFunctionsText;
  };


  return <div ref={wrapper}></div>;
};

